.listViewContainer {
  height: 700px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.listViewLeftSection {
  height: 100%;
  width: 15%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-right: 2px solid #dddddd;
}

.leftSectionItem {
  margin-bottom: 3px;
  cursor: pointer;
  padding: 3px;
  color: #dddddd65;
}

.leftSectionAcitveItem {
  margin-bottom: 3px;
  padding: 3px;
  cursor: pointer;
  background: rgba(32, 32, 32, 0.2);
  border-radius: 3px;
  color: #f0ecec;
}

.activeNoteContainer {
  height: 100%;
  width: 85%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.activeNote {
  width: 50%;
}

.activeNoteTitle {
  height: 30px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  border-bottom: 2px solid #dddddd;
}

.activeNoteItem {
  display: -ms-flexbox;
  display: flex;
}


