.App {
  min-height: 100%;
  width: 100%;
  background: #0F2027;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, left top, left bottom, from(#2C5364), color-stop(#203A43), to(#0F2027));
background: -webkit-linear-gradient(top, #2C5364, #203A43, #0F2027);
background: -o-linear-gradient(top, #2C5364, #203A43, #0F2027);
background: linear-gradient(to bottom, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

      /*
  background: -webkit-linear-gradient(to right, #243B55, #141E30);
  background: linear-gradient(to right, #243B55, #141E30);
  */
  
  color: #dddddd;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  font-family: 'Tajawal', sans-serif;
  font-size: 18px;
}


