.signInContainer {
  height: 650px;
  width: 500px;
  background: darkblue;
  position: fixed;
  top: calc(-50% - 400px);
  left: calc(50% - 250px);
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.signInInput {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 190px;
  padding-left: 5px;
  padding-right: 5px;
}

.signInButton {
  background: #222222;
  width: 200px;
  margin-top: 5px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.signInButton:hover {
  background: #147c2e;
  border-radius: 5px;
}

.closeButton {
  position: absolute;
  top: 0px;
  right: 0px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  padding-right: 5px;
}


