
.newTodoContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

/* .newTodoInput */
input {
  outline: none;
  border: 0;
  padding: 0;
  margin: 0;
  background: #333333;
  height: 30px;
  color: #dddddd;
  line-height: 30px;
  padding-left: 5px;
  font-family: 'Tajawal', sans-serif;
  font-size: 18px;
}

.newTodoSubmit {
  height: 28px;
  line-height: 30px;
  text-align: center;
  margin-left: 20px;
  cursor: pointer;
  border: 1px solid #444444;
  padding-left: 5px;
  padding-right: 5px;

}


