.topNavContainer {
  height: 75px;
  width: 100%;
  background: #222222;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: 0px 0px 25px 5px rgba(17,17,17,1);
          box-shadow: 0px 0px 25px 5px rgba(17,17,17,1);
}

.topNavButton {
  height: 35px;
  width: 75px;
  border: 2px solid #dddddd;
  margin-left: 10px;
  margin-right: 10px;
  line-height: 35px;
  text-align: center; 
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.topNavButton:hover {
  background: #dddddd;
  color: #111111;
  border-radius: 5px;
}

.left, .right {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.right {
  -ms-flex-pack: end;
      justify-content: flex-end;
}


